import { Component, h, Prop, State, Event, EventEmitter } from '@stencil/core';
import { cart } from '../../../global/data/cart';
import { common } from '../../../global/data/common';

@Component({
  tag: 'ks-cart-deal',
  styleUrl: 'deal.css'
})
export class CartDeal {
    @Prop({reflect: true}) ikey: string;
    @Prop() name: string;
    @Prop() img: string;
    @Prop() link: string;
    @Prop() price: string;

    @State() loading: boolean = false;

    @Event() addDeal: EventEmitter<string>;
    Add() {
        this.addDeal.emit(this.ikey);
    }

    render() {
        const translations = common.get('translations');

        return[
            <a href={this.link}>
                <div class="image">
                    <div class="circle center">
                        <ks-img src={this.img} alt={this.name} vertical center></ks-img>
                    </div>

                    <svg class="fx fx1 center" width="250" height="250">
                        <circle cx="125" cy="125" r="80" fill="transparent" stroke="white" stroke-width="3" stroke-dasharray="50, 32" />
                    </svg>

                    <svg class="fx fx2 center" width="250" height="250">
                        <circle cx="125" cy="125" r="88" fill="transparent" stroke="white" stroke-width="3" stroke-dasharray="50,59" />
                    </svg>

                    <svg class="fx fx3 center" width="250" height="250">
                        <circle cx="125" cy="125" r="96" fill="transparent" stroke="white" stroke-width="3" stroke-dasharray="30,30" />
                    </svg>

                    <svg class="fx fx4 center" width="250" height="250">
                        <circle cx="125" cy="125" r="106" fill="transparent" stroke="white" stroke-width="3" stroke-dasharray="70,60" />
                    </svg>
                </div>
            </a>,

            <div class="text ks-text-decorated">
                <div class="top"><a href={this.link}>{this.name}</a></div>
                <div class="bottom">
                    {this.price}

                    <button class="ks-text-decorated small" onClick={()=>this.Add()}>
                        {cart.get('loadingDeals') ? <div uk-spinner="ratio: 0.8"></div> : <span>{translations.addToCart}</span>}
                    </button>
                </div>
            </div>,

            <button class="ks-text-decorated large" onClick={()=>this.Add()}>
                {cart.get('loadingDeals') ? <div uk-spinner="ratio: 0.8"></div> : <span>{translations.addToCart}</span>}
            </button>
        ];
    }
}